.contacts
{
    font-size: 24px;

    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;

    height: 30vh;
}

.main
{
    font-size: 24px;

    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;

    width: 90%;
    height: 100%;

    /*color: rgba(55, 55, 55, .78);*/
    /*border: 5px solid;*/
    /*background-color: #000;*/
}

.title
{
    font-size: 18px;

    margin: 20px;

    color: white;
}

.wrapper
{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-evenly;

    width: 90%;
    height: 90%;
}

.form
{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;

    width: 360px;
    /*border: 1px solid;*/
    height: 30px;
    margin: 5px;

    background-color: darkgray;
}

.buttonClick
{
    font-size: 10pt;
  margin: 15px;
    width: 80px;
    height: 30px;

    color: purple;
    border-radius: 10px;
    background-color: lightgreen;
}

@media screen and (max-width: 500px)
{
    .wrapper
    {
        width: 100%;
    }
}

@media screen and (max-width: 500px)
{
    .form
    {
        width: 250px;
        height: 30px;
    }
}
